import React, {useEffect, useState} from "react";
import styles from "../order-details/order-details.module.css";
import doneIconPath from "../../images/done.png"
import loaderIconPath from "../../images/loader.gif"
import {useSelector} from "react-redux";

function OrderDetails() {
    const orderId = useSelector(store => store.orderDetails.orderId)
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if(orderId) {
            setLoading(false);
        }
    }, [orderId]);

    if (loading) {
        return (<div className={`${styles.content} pt-10 pr-10 pl-10 pb-4`}>
            <h4 className="text text_type_main-medium pt-30 pb-3">  ,</h4>
            <p className="text text_type_main-medium pb-15">   ...</p>
            <img src={loaderIconPath} className="pb-15"/>
        </div>)

    }

    return (<div className={`${styles.content} pt-10 pr-10 pl-10 pb-4`}>
        <h4 className="text text_type_digits-large pt-30 pb-8">{orderId}</h4>
        <p className="text text_type_main-medium pb-15"> </p>
        <img src={doneIconPath} className="pb-15"/>
        <p className="text text_type_main-default">   </p>
        <p className="text text_type_main-default text_color_inactive">   
            </p>
    </div>)
}


export default OrderDetails